import React from "react";
import { useState } from "react";
import "./style.css"
import Layout from "../layout/layout"

const PartnerFirm = () => {

    const [details, setDetails] = useState({
        Email: "",
        Mobile: "",
        City: "",
    })


    const Handleclick = (e) => {
        const name = e.target.name
        const value = e.target.value

        setDetails({ ...details, [name]: value })

    }

    const [record, setRecord] = useState([])




    const HandleSubmit = (e) => {
        e.preventDefault();

        const newData = { ...details }
        setRecord({ ...record, newData })
    }


    return (
        <Layout>
        <section className="body">
            <div className="left-wing">
                <h2>Partnership firm registration</h2>
                <list>
                    <ul>
                        <li>Partnership firm registration deed drafted within 3 days , completely online and hasslefree </li>
                        <li>Transparent process through follow-up and regular updates</li>
                    </ul>
                </list>
            </div>
            <div className="right-wing">
                <p className="form-heading">Get Started!</p>
                <form className="signup-form">
                    <div>
                        <input
                            type="email"
                            placeholder="Email"
                            autoComplete="off"
                            onChange={Handleclick}
                            value={details.Email}
                            name="email"
                        />
                    </div>
                    <div>
                        <input
                            type="Mobile"
                            placeholder="Mobile No."
                            autoComplete="off"
                            onChange={Handleclick}
                            value={details.Mobile}
                            name="mobile"
                        />
                    </div>
                    <div>
                        <input
                            type="City"
                            placeholder="City"
                            autoComplete="off"
                            onChange={Handleclick}
                            name="city"
                            value={details.City}
                        />
                    </div>
                </form>
                <button onClick={HandleSubmit}>Consult an expert</button>
            </div>
        </section>
        </Layout>
    )
}

export default PartnerFirm;